* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}

h1,
h2,
h3,
h4,
h5,
h6,
p {
    margin: 0;
}

body {
    position: relative;

    .section1 {
        padding: 50px 0;

        .container {
            .header1 {
                display: flex;
                justify-content: space-between;
                align-items: center;
                margin-bottom: 70px;

                .menu1 {
                    display: flex;
                    justify-content: center;
                    align-items: center;

                    ul {
                        padding: 0 !important;
                        margin-left: 30px;
                        margin-bottom: 0;
                        display: flex;
                        justify-content: center;
                        align-items: center !important;

                        li {
                            list-style-type: none;

                            a {
                                color: white;
                                font-family: "Gilroy";
                                font-size: 20px;
                                text-decoration: none !important;
                            }

                            a::after {
                                width: 0;
                                content: "";
                                display: block;
                                height: 2px;
                                background: #ffffff;
                                -webkit-transition: width .3s;
                                -o-transition: width .3s;
                                transition: width .3s;
                            }

                            a:hover::after {
                                width: 100%;
                            }

                        }
                    }
                }

                .phone_menu {
                    width: 130px;
                    height: 40px;
                    background: linear-gradient(to right, #00FFA3, #33C1FF);
                    border-radius: 10px;
                    padding: 0 20px;
                    display: none;
                    justify-content: space-between;
                    align-items: center;
                    text-decoration: none !important;

                    h5 {
                        color: rgb(0, 0, 0);
                        font-family: "Gilroy";
                        font-weight: 800;
                        text-transform: uppercase;
                        font-size: 18px;
                        margin-right: 10px;
                    }

                    .menu_ico {
                        display: flex;
                        justify-content: center;
                        align-items: center;
                        flex-direction: column;

                        div {
                            margin-top: 1px;
                            margin-bottom: 1px;
                            width: 20px;
                            height: 4px;
                            border-radius: 10px;
                            background: #000;
                        }
                    }
                }

                .menu2 {
                    position: absolute;
                    display: none;
                    flex-direction: column;
                    align-items: center;
                    top: 100px;
                    left: 50%;
                    margin-left: -200px;

                    a {
                        text-decoration: none !important;
                    }

                    ul {
                        display: flex;
                        justify-content: center;
                        align-items: center;
                        margin-bottom: 20px;
                        padding: 15px;
                        width: 400px;
                        background: rgba(255, 255, 255, 0.075);

                        li {
                            list-style-type: none;

                            h5 {

                                color: rgb(255, 255, 255);
                                font-family: "Gilroy";
                                font-weight: 600;
                                font-size: 18px;
                            }
                        }
                    }
                }
            }
        }
    }
}


// =====================MAIN-TEXT===================
.main_text {
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-bottom: 68px;

    .main_h1 {
        display: flex;
        justify-content: center;
        align-items: flex-start;
        flex-direction: column;

        h1 {
            font-family: Gilroy;
            font-style: normal;
            font-weight: 600;
            font-size: 80px;
            line-height: 95px;
            color: white;
        }

        h2 {
            font-family: Gilroy;
            font-style: normal;
            font-weight: 600;
            font-size: 40px;
            line-height: 48px;
            color: #33C1FF;
            margin-bottom: 45px;
        }

        h3 {
            font-family: Gilroy;
            font-style: normal;
            font-weight: 600;
            font-size: 28px;
            line-height: 36px;
            color: #FFFFFF;
            margin-bottom: 45px;
        }

        a {
            display: flex;
            justify-content: center;
            align-items: center;
            text-decoration: none;
            font-family: Gilroy;
            width: 300px;
            height: 60px;
            background: rgba(255, 255, 255, 0.1);
            border-radius: 200px;
            font-style: normal;
            font-weight: 600;
            font-size: 22px;
            line-height: 27px;
            text-align: center;
            color: #FFFFFF;
            transition: 0.5s;
        }

        a:hover{
            background: rgba(255, 255, 255);
            color: black;
        }
    }

    .main_img {
        display: flex;
        justify-content: flex-end;
        align-items: center;
    }
}

@media (max-width: 600px) {
    .menu2 {
        display: flex;
    }

    .menu1 {
        display: none !important;
    }

    .phone_menu {
        display: flex !important;
    }

    body .header1 {
        justify-content: space-around !important;
    }
}