* {
  margin: 0;
  padding: 0;
  -webkit-box-sizing: border-box;
  box-sizing: border-box;
}

h1,
h2,
h3,
h4,
h5,
h6,
p {
  margin: 0;
}

body {
  position: relative;
}

body .section1 {
  padding: 50px 0;
}

body .section1 .container .header1 {
  display: -webkit-box;
  display: -webkit-flex;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: justify;
  -webkit-justify-content: space-between;
  -ms-flex-pack: justify;
  justify-content: space-between;
  -webkit-box-align: center;
  -webkit-align-items: center;
  -ms-flex-align: center;
  align-items: center;
  margin-bottom: 70px;
}

body .section1 .container .header1 .menu1 {
  display: -webkit-box;
  display: -webkit-flex;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: center;
  -webkit-justify-content: center;
  -ms-flex-pack: center;
  justify-content: center;
  -webkit-box-align: center;
  -webkit-align-items: center;
  -ms-flex-align: center;
  align-items: center;
}

body .section1 .container .header1 .menu1 ul {
  padding: 0 !important;
  margin-left: 30px;
  margin-bottom: 0;
  display: -webkit-box;
  display: -webkit-flex;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: center;
  -webkit-justify-content: center;
  -ms-flex-pack: center;
  justify-content: center;
  -webkit-box-align: center !important;
  -webkit-align-items: center !important;
  -ms-flex-align: center !important;
  align-items: center !important;
}

body .section1 .container .header1 .menu1 ul li {
  list-style-type: none;
}

body .section1 .container .header1 .menu1 ul li a {
  color: white;
  font-family: "Gilroy";
  font-size: 20px;
  text-decoration: none !important;
}

body .section1 .container .header1 .menu1 ul li a::after {
  width: 0;
  content: "";
  display: block;
  height: 2px;
  background: #ffffff;
  -webkit-transition: width 0.3s;
  -o-transition: width 0.3s;
  transition: width 0.3s;
}

body .section1 .container .header1 .menu1 ul li a:hover::after {
  width: 100%;
}

body .section1 .container .header1 .phone_menu {
  width: 130px;
  height: 40px;
  background: -webkit-gradient(linear, left top, right top, from(#00FFA3), to(#33C1FF));
  background: -o-linear-gradient(left, #00FFA3, #33C1FF);
  background: linear-gradient(to right, #00FFA3, #33C1FF);
  border-radius: 10px;
  padding: 0 20px;
  display: none;
  -webkit-box-pack: justify;
  -webkit-justify-content: space-between;
  -ms-flex-pack: justify;
  justify-content: space-between;
  -webkit-box-align: center;
  -webkit-align-items: center;
  -ms-flex-align: center;
  align-items: center;
  text-decoration: none !important;
}

body .section1 .container .header1 .phone_menu h5 {
  color: #000000;
  font-family: "Gilroy";
  font-weight: 800;
  text-transform: uppercase;
  font-size: 18px;
  margin-right: 10px;
}

body .section1 .container .header1 .phone_menu .menu_ico {
  display: -webkit-box;
  display: -webkit-flex;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: center;
  -webkit-justify-content: center;
  -ms-flex-pack: center;
  justify-content: center;
  -webkit-box-align: center;
  -webkit-align-items: center;
  -ms-flex-align: center;
  align-items: center;
  -webkit-box-orient: vertical;
  -webkit-box-direction: normal;
  -webkit-flex-direction: column;
  -ms-flex-direction: column;
  flex-direction: column;
}

body .section1 .container .header1 .phone_menu .menu_ico div {
  margin-top: 1px;
  margin-bottom: 1px;
  width: 20px;
  height: 4px;
  border-radius: 10px;
  background: #000;
}

body .section1 .container .header1 .menu2 {
  position: absolute;
  display: none;
  -webkit-box-orient: vertical;
  -webkit-box-direction: normal;
  -webkit-flex-direction: column;
  -ms-flex-direction: column;
  flex-direction: column;
  -webkit-box-align: center;
  -webkit-align-items: center;
  -ms-flex-align: center;
  align-items: center;
  top: 100px;
  left: 50%;
  margin-left: -200px;
}

body .section1 .container .header1 .menu2 a {
  text-decoration: none !important;
}

body .section1 .container .header1 .menu2 ul {
  display: -webkit-box;
  display: -webkit-flex;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: center;
  -webkit-justify-content: center;
  -ms-flex-pack: center;
  justify-content: center;
  -webkit-box-align: center;
  -webkit-align-items: center;
  -ms-flex-align: center;
  align-items: center;
  margin-bottom: 20px;
  padding: 15px;
  width: 400px;
  background: rgba(255, 255, 255, 0.075);
}

body .section1 .container .header1 .menu2 ul li {
  list-style-type: none;
}

body .section1 .container .header1 .menu2 ul li h5 {
  color: #ffffff;
  font-family: "Gilroy";
  font-weight: 600;
  font-size: 18px;
}

